import React, { useState, useRef, useEffect } from "react";
import { Form } from "react-bootstrap";
import styles2 from "../../css/COLUMN.module.css";
import ElemDeleteControl from "../ElemDeleteControlWrapper";
import { makeStyles } from "@material-ui/core/styles";
import { checkValid } from "../../../Resources/globalFunctions";
import SvgDefaultImg from "../../../Icons/Image/defaultImg.svg";
import QuickTools from "../../../ReusableComponents/QuickTools/QuickTools";
import { triggerEventCalls } from "../../../ReusableComponents/Events/EventExecutionLogic";
import { getSuggestedId } from "../../../components/IFormDesignerLayout/Logic/UserIdLogic";
import CustomizedAlert from "../../../ReusableComponents/Alert/CustomizedAlert";
import { handleDisplayProperty, updatePartialJSON } from "../../../components/IFormDesignerLayout/IFormDesignerLogic";
import { GetState, GetStateFromData } from "../../../components/CustomHooks/CustomHooks";
import EditableLabel from "../../../ReusableComponents/EditableLabel/EditableLabel";

const useStyles = makeStyles({
  mainDiv: {
    padding: "2px",
    display: "flex",
    flexDirection: (style) =>
      style.LabelInputAlignment === "topDown"
        ? "column"
        : style.LabelInputAlignment === "downTop"
        ? "column-reverse"
        : style.LabelInputAlignment === "leftRight"
        ? "row"
        : style.LabelInputAlignment === "rightLeft"
        ? "row-reverse"
        : "column"
  },
  imageParent: {
    width: "100%",
    textAlign: window?.appformBuilder_configs?.selectedLanguage === "ar" || window?.appformBuilder_configs?.selectedLanguage === "ar-SA" ? "right" : ((style) => style.imageAlignment || "left")
  }
});

function Image(props) {
  const contexts = GetStateFromData(props.containerId);
  const {controlSelected,tempCanWidth} = GetState("controlSelected","tempCanWidth");
  const { IdPrecedence, deleteControl, formState2, openQuickToolFor, device, imageBaseURL } = contexts.contexts;
  const reduxContexts = contexts;
  const style = props.data.style;
  const data = props.data;
  const isSubformInPage = props?.isSubformInPage || false;
  const defLabel = props?.data?.style?.["Label"] || "";
  const themeData = formState2?.theme;
  const imageParentSpanId = "imageParentSpan";
  const actualControlId = props.data.id;
  const mappingId = props.data?.["style"]["MappingId"];
  const userDefinedId = props.data?.["style"]["CustomId"];
  const conditionalQuickToolOpening =
    props.id === openQuickToolFor ? true : false

  const classes = useStyles(style);

  const [editLabel, setEditLabel] = useState(false);
  const [alertData, setAlertData] = useState({ open: false, showConfirm: "showNotification" });
  const [defaultLabel, setDefaultLabel] = useState(defLabel);
  const [open, setOpen] = useState(conditionalQuickToolOpening);
  const [mobileDisplay, setMobileDisplay] = useState(true);
  const [tabDisplay, setTabDisplay] = useState(true);
  const [imageStyle, setImageStyle] = useState(props?.data?.style?.["imageStyle"] || {});

  //dummy state for render
  const [rerenderState, setReRenderState] = useState(false);

  const bringEditableLabel = () => setEditLabel(true);

  function showAlertConfirmNotifications(dataTobeSet) {
    setAlertData(dataTobeSet);
  }

  useEffect(() => setDefaultLabel(props?.data?.style?.["Label"]), [props?.data?.style?.["Label"]]);

  const suggestedId = getSuggestedId(actualControlId, mappingId, userDefinedId, IdPrecedence);

  const imageRefrence = useRef(null);
  const parentSpanRefrence = useRef(null);
  const imageLabelDefaultStyles = useRef({
    font_family: "Open Sans",
    font_size: "12px",
    font_color: "#000",
    font_weight: "bold",
    font_style: "normal"
  });

  const setImageLabelStyles = (newStyle) => {
    const prevState = JSON.stringify(imageLabelDefaultStyles.current);
    imageLabelDefaultStyles.current = { ...imageLabelDefaultStyles.current, ...newStyle };
    if(!props.previewmode){
      if(prevState !== JSON.stringify(imageLabelDefaultStyles.current)){
        setReRenderState(!rerenderState)
      }
    }
  };

  useEffect(() => {
    if (themeData?.["BasicTheme"]?.["FontFamily"]) {
      setImageLabelStyles({
        font_family:
          themeData["BasicTheme"]["FontFamily"] === "Select" ? "Open Sans" : themeData["BasicTheme"]["FontFamily"]
      });
    }
    if (themeData?.["BasicTheme"]?.["FontSize"]) {
      setImageLabelStyles({ font_size: themeData["BasicTheme"]["FontSize"] });
    }
  }, [themeData?.["BasicTheme"], style?.["renderTheme"]]);

  useEffect(() => {
    if (props.data.style["imageStyle"] === undefined || props.data.style["imageStyle"] === null) {
      props.data.style.imageStyle = {
        ...props.data.style?.imageStyle,
        height: props.data.style.height,
        width: props.data.style.width,
        defaultWidth: props.data.style.defaultWidth,
        defaultHeight: props.data.style.defaultHeight,
        size: props.data.style.size,
        scale: "1",
        resize: props.data.style.resize,
        widthType: props.data.style.scale === "pixels" ? "px" : "%",
        aspectRatio: "auto",
        transform: "rotate(0deg) scale(1,1)",
        degree: 0,
        scaleX: 1,
        scaleY: 1
      };
    }
    updatePartialJSON(props?.data?.id,props?.data);
    setImageStyle(props?.data?.style?.imageStyle || {});
  }, [props.data.style, props.data.style.imageStyle]);

  const startMobile = useRef();
  const endMobile = useRef();
  const startTab = useRef();
  const endTab = useRef();
  
  useEffect(() => {
    if (formState2?.style) {
      startMobile.current = parseInt(formState2.style?.["Breakpoint"]?.["Mobile"]?.["StartValue"]);
      endMobile.current = parseInt(formState2.style?.["Breakpoint"]?.["Mobile"]?.["EndValue"]);
      startTab.current = parseInt(formState2.style?.["Breakpoint"]?.["Tab"]?.["StartValue"]);
      endTab.current = parseInt(formState2.style?.["Breakpoint"]?.["Tab"]?.["EndValue"]);
    }
  }, [
    formState2.style?.["Breakpoint"],
    formState2.style?.["Breakpoint"]?.["Mobile"],
    formState2.style?.["Breakpoint"]?.["Tab"]
  ]);

  useEffect(() => {
    if (props?.previewmode)
      handleDisplayProperty(
        props,
        device,
        setMobileDisplay,
        setTabDisplay,
        startMobile.current,
        endMobile.current,
        startTab.current,
        endTab.current
      );
  }, [
    props?.data?.style?.["MobileVisible"],
    props?.data?.style?.["TabsVisible"],
    device,
    endMobile,
    endTab,
    startMobile,
    startTab,
    tempCanWidth
  ]);
  
  useEffect(() => {
    if (props?.previewmode && document.getElementById("imageParentSpan")) {
      document.getElementById("imageParentSpan").ondragstart = () => {
        return false;
      };
    }
  }, [props?.previewmode])
  const setLabelHandler = (recievedLabel) => {
    try {
      if (props.data?.style?.Label || props.data?.id) {
        setDefaultLabel((prevDefaultLabel) => {
          if (prevDefaultLabel === recievedLabel) {
            return prevDefaultLabel;
          } else {
            props.data.style["Label"] = recievedLabel;
            return recievedLabel;
          }
        });
        setEditLabel(false);
      }
    } catch (err) {
      console.error(err);
    }
  };

  const handleImageClick = (event) => {
    if (props.data?.event?.Events?.Click)
      triggerEventCalls(
        event,
        {id:props?.data?.id},
        props.data.event,
        props.data.elem_type,
        showAlertConfirmNotifications,
        reduxContexts
      );
  };

  const labelStyle = {
    color: style?.FontColor || imageLabelDefaultStyles.current.font_color,
    fontFamily: style?.FontFamily || imageLabelDefaultStyles.current.font_family,
    fontSize: style?.FontSize || imageLabelDefaultStyles.current.font_size,
    fontWeight: style?.FontWeight || imageLabelDefaultStyles.current.font_weight,
    fontStyle: style?.FontStyle || imageLabelDefaultStyles.current.font_style,
    lineHeight: 1,
    "whiteSpace":"break-spaces"
  };

  const padding_left = props.data?.style?.["SpacingLeft"] || "0px";
  const padding_right = props.data?.style?.["SpacingRight"] || "0px";
  const padding_top = props.data?.style?.["SpacingTop"] || "0px";
  const padding_bottom = props.data?.style?.["SpacingBottom"] || "0px";

  const inputStylePadding = {
    paddingLeft: padding_left,
    paddingRight: padding_right,
    paddingTop: padding_top,
    paddingBottom: padding_bottom,
    display: "flex"
  };

  if (props.data?.style?.LabelInputAlignment) {
    if (props.data.style.LabelInputAlignment == "leftRight") {
      inputStylePadding.flexDirection = "row";
      labelStyle.marginRight = "1rem";
    } else if (props.data.style.LabelInputAlignment == "rightLeft") {
      inputStylePadding.flexDirection = "row-reverse";
      labelStyle.marginLeft = "1rem";
    } else if (props.data.style.LabelInputAlignment == "downTop") {
      inputStylePadding.flexDirection = "column-reverse";
      labelStyle.marginTop = "4px";
      labelStyle.marginBottom = "0px";
    }
  }

  const parseNum = (num) => (isNaN(parseFloat(num)) ? 1 : parseFloat(num));

  const imgStyle = {
    width:
      imageStyle?.resize === "fullWidth"
        ? `${parseNum(imageStyle?.enlarge || 1) * 100}%`
        : imageStyle?.widthType === "px"
        ? imageStyle?.width + imageStyle?.widthType
        : imageStyle?.size + imageStyle?.widthType,
    height:
      imageStyle.aspectRatio !== "auto"
        ? "auto"
        : imageStyle?.widthType === "px"
        ? imageStyle?.height + imageStyle?.widthType
        : imageStyle?.size + imageStyle?.widthType,
    transform: imageStyle.transform,
    aspectRatio: imageStyle?.aspectRatio,
    margin: "auto",
    borderColor: style?.BorderColor || "#c4c4c4",
    borderWidth: style?.BorderWidth || "0px",
    borderRadius: style?.BorderRadius || "0px",
    borderStyle: "solid",
    maxWidth: "100%"
  };

  const imgSorce =
    style.uploadType === "file"
      ? (checkValid(style.iconFile) && `${imageBaseURL}${checkValid(style.iconFile)}`) || SvgDefaultImg
      : (checkValid(style.imageUrl) && `${imageBaseURL}${checkValid(style.imageUrl)}`) || SvgDefaultImg;

  if (props.previewmode) {
    return style?.Visible === "true" ? (
      <Form.Group
        id={suggestedId + "_group"}
        style={mobileDisplay === false || tabDisplay === false ? { display: "none" } : inputStylePadding}
        key={props.id}
        frameindex={props.data.frameIndex}
        elementindex={props.data.elementIndex}
        sheetIndex={props.data.sheetIndex}
        sheetElementIndex={props.data.sheetElementIndex}
        sheetFrameElementindex={props.data.sheetFrameElementindex}
        control_id={props.id}
        className={`${styles2.formStyle} ${classes.mainDiv} spacingThemeControls`}
        elem_type="image"
        tabIndex="1"
      >
        <Form.Label
          id={suggestedId + "_label"}
          elem_type="image"
          className={" spacingThemeLabel "}
          style={labelStyle}
          control_id={props.data.id}
          frameindex={data.frameIndex}
          elementindex={data.elementIndex}
        >
          {props.data.style["Label"]}
        </Form.Label>

        <span
          control_id={props.id}
          className={classes.imageParent}
          id={imageParentSpanId}
          ref={parentSpanRefrence}
          tabIndex="-1"
        >
          <img
            style={imgStyle}
            id={suggestedId}
            ref={imageRefrence}
            onClick={handleImageClick}
            alt="image1"
            src={imgSorce}
          />
        </span>
      </Form.Group>
    ) : null;
  } else {
    return (
      <Form.Group
        id={suggestedId + "_group"}
        onClick={isSubformInPage ? {} : (e) => props.handleSelected(e, props.id, "n")}
        onKeyDown={(e) => props.handleSelected(e)}
        
        key={props.id}
        frameindex={props.data.frameIndex}
        elementindex={props.data.elementIndex}
        sheetIndex={props.data.sheetIndex}
        sheetElementIndex={props.data.sheetElementIndex}
        sheetFrameElementindex={props.data.sheetFrameElementindex}
        control_id={props.id}
        className={`${styles2.formStyle} ${classes.mainDiv} spacingThemeControls`}
        style={inputStylePadding}
        elem_type="image"
        tabIndex="1"
      >
        <EditableLabel
          defaultLabel={
            <Form.Label
              id={suggestedId + "_label"}
              elem_type="image"
              onDoubleClick={bringEditableLabel}
              className={"spacingThemeLabel "}
              style={labelStyle}
              control_id={props.data.id}
              frameindex={data.frameIndex}
              elementindex={data.elementIndex}
            >
              {props.data.style["Label"] || ""}
            </Form.Label>
          }
          editlabel={editLabel}
          default_value={defaultLabel}
          setLabelHandler={(recievedLabel) => setLabelHandler(recievedLabel)}
          style={labelStyle}
        />

        <span control_id={props.id} className={classes.imageParent} id={imageParentSpanId} ref={parentSpanRefrence}>
          <img id={suggestedId} src={imgSorce} style={imgStyle} ref={imageRefrence} alt="Control Picture" />
        </span>
        {controlSelected?.id === props?.id &&
        <ElemDeleteControl
          isSubformInPage={isSubformInPage}
          containerId={props.containerId}
          id={props.id}
          data={props.data}
          deleteControl={deleteControl}
          elemType={props.data?.elem_type}
          setDragging={props.setDragging}
          handleReorderStart={props.handleReorderStart}
          handleReorderDrop={props.handleReorderDrop}
          dragId={props.dragId}
          dragType={"column"}
        />
  }
        {window?.openQuickTools && open &&  (
          <QuickTools containerId={props.containerId} selectedControl={props.data} open={open} setOpen={setOpen} />
        )}
        {
              alertData.open === true &&
            <CustomizedAlert alertData={alertData} />
            }
      </Form.Group>
    );
  }
}

export default Image;
